/* Carousel Container */
.carousel-container {
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  font-family: "Poppins", sans-serif;
  height: 100vh;
  display: flex;
  align-items: center;
}

/* Carousel */
.carousel {
  display: flex;
  width: 100%;
  gap: 1rem;
}

/* Carousel Panel */
.carousel-panel {
  position: relative;
  height: 80vh;
  border-radius: 25px;
  cursor: pointer;
  flex: 0.5;
  transition: all 0.7s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
  margin-top: 70px;
}

.carousel-panel.active {
  flex: 5;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

/* Background Image */
.carousel-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  filter: grayscale(0.8) blur(2px);
  transition: filter 0.7s cubic-bezier(0.4, 0, 0.2, 1);
}

.carousel-panel.active .carousel-background {
  filter: none;
}

/* Slide Heading */
.slide-heading {
  position: absolute;
  right: 3.5rem !important;
  top: 2rem;
  color: rgba(255, 255, 255, 0.9);
  font-size: 12px;
  font-weight: 700;
  transform: rotate(-90deg);
  transform-origin: right top;
  transition: all 0.5s ease;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  white-space: nowrap;
  z-index: 2; /* Ensure heading is above the blurred background */
}

.slide-heading.active {
  right: auto;
  top: 2rem;
  transform: none;
  font-size: 13px;
  letter-spacing: 3px;
  color: #fff;
}

/* Progress Line */
.progress-line {
  width: 70%;
  height: 2px;
  background: rgba(255, 255, 255, 0.3);
  overflow: hidden;
  margin-bottom: 20px;
}

.progress-fill {
  height: 100%;
  background: #fff;
  transition: width 0.05s linear;
}

/* Carousel Content */
.carousel-content {
  position: absolute;
  bottom: 4rem;
  left: 4rem;
  max-width: 600px;
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease 0.3s;
  z-index: 2; /* Ensure content is above the blurred background */
}

.carousel-panel.active .carousel-content {
  opacity: 1;
  transform: translateY(0);
}

.carousel-content h1 {
  font-size: 40px;
  font-weight: 600 !important;
  margin-bottom: 1.5rem;
  color: #fff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3) !important;
}

.carousel-content p {
  font-size: 14px;
  margin-bottom: 2rem;
  color: rgba(255, 255, 255, 0.9);
}

/* CTA Button */
.cta-button10 {
  background: rgba(255, 255, 255, 0.9);
  color: #2d2d2d;
  border: none;
  padding: 5px 10px;
  border-radius: 30px;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.cta-button10:hover {
  background: #fff;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Carousel Controls */
.carousel-controls {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  display: flex;
  gap: 1rem;
  z-index: 2; /* Ensure controls are above the blurred background */
}

.carousel-control {
  border: none;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  background: transparent !important;
  color: #fff !important;
}

.carousel-control:hover {
  background: #fff;
  transform: scale(1.1);
}

.carousel-control i {
  font-size: 1.2rem;
  color: #2d2d2d;
}

/* Keyframes and Responsive */
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@media (max-width: 768px) {
  .carousel {
    width: 90vw;
    gap: 0.5rem;
  }

  .carousel-panel {
    height: 70vh;
    border-radius: 15px;
  }

  .slide-heading {
    font-size: 1rem;
    right: -2rem;
  }

  .slide-heading.active {
    font-size: 1.2rem;
    left: 1.5rem;
  }

  .carousel-content {
    bottom: 2rem;
    left: 2rem;
    max-width: 80%;
  }

  .carousel-content h1 {
    font-size: 1.8rem;
  }

  .carousel-content p {
    font-size: 1rem;
  }

  .cta-button10 {
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
  }

  .carousel-controls {
    bottom: 1rem;
    right: 1rem;
  }

  .carousel-control {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 480px) {
  .carousel-panel {
    height: 75vh;
  }

  .slide-heading {
    display: none;
  }

  .carousel-content h1 {
    font-size: 1.5rem;
  }

  .carousel-content p {
    font-size: 0.9rem;
  }

  .cta-button10 {
    padding: 0.6rem 1.2rem;
  }
}

/* Mobile Styles */
@media (max-width: 768px) {
  .carousel {
    gap: 0;
    margin-top: 50px !important;
  }

  .carousel-panel:not(.active) {
    display: none;
  }

  .carousel-panel.active {
    flex: 1;
    margin: 0 10px;
    filter: none;
  }

  .carousel-content {
    left: 2rem !important;
    bottom: 2rem !important;
    max-width: 80% !important;
  }

  .carousel-content h1 {
    font-size: 1.8rem !important;
  }

  .carousel-content p {
    font-size: 1rem !important;
  }

  .slide-heading {
    display: none;
  }
}