/* Base Styles */
.app-dev-page {
    font-family: 'Inter', sans-serif;
    color: #2c3e50;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  /* Hero Section */
  .app-dev-hero {
    /* background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../assets/tech-bg.jpg); */
    background-size: cover;
    background-position: center;
    /* color: white; */
    padding: 120px 0;
    text-align: center;
  }
  
  .hero-content h1 {
    font-size: 3.5rem;
    margin-bottom: 20px;
  }
  
  .hero-content p {
    font-size: 1.25rem;
    margin-bottom: 40px;
  }
  
  /* Technologies Section */
  .tech-section7 {
    text-align: center;
    padding: 80px 0;
    background: #f9f9f9;
  }
  
  .tech-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 30px;
    margin-top: 40px;
  }
  
  .tech-card {
    background: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    text-align: center;
  }
  
  .tech-card:hover {
    transform: translateY(-10px);
  }
  
  .tech-card img {
    height: 60px;
    margin-bottom: 15px;
  }
  
  /* Services Section */
  .offer-section {
    padding: 80px 0;
  }
  
  .offer-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
    margin-top: 40px;
  }
  
  .offer-card {
    padding: 30px;
    border-radius: 10px;
    background: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease;
    text-align: center;
  }
  
  .offer-card:hover {
    transform: translateY(-5px);
  }
  
  .offer-card i {
    font-size: 2.5rem;
    color: #0d6efd;
    margin-bottom: 20px;
  }
  
  /* Testimonials */
  .testimonials {
    padding: 80px 0;
    background: #f9f9f9;
  }
  
  .testimonial-slider {
    display: flex;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    padding: 20px 0;
  }
  
  .testimonial-card {
    flex: 0 0 80%;
    margin-right: 20px;
    background: white;
    border-radius: 10px;
    padding: 30px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    scroll-snap-align: start;
  }
  
  .testimonial-card img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin-bottom: 20px;
  }
  
  /* Process Timeline */
  .process-section {
    padding: 80px 0;
    background: white !important;
  }
  
  .process-timeline {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 40px;
    position: relative;
    margin-top: 40px;
  }
  
  .process-step {
    text-align: center;
    padding: 30px;
    background: #f9f9f9 !important;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  }
  
  .step-icon {
    width: 60px;
    height: 60px;
    background: #0d6efd;
    border-radius: 50%;
    margin: 0 auto 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1.5rem;
  }

  
  /* CTA Section */
  .cta-section {
    padding: 80px 0;
    /* background: #0d6efd; */
    background: #f9f9f9;
    color: white;
    text-align: center;
  }
  
  .cta-button {
    display: inline-block;
    padding: 15px 40px;
    background: #0d6efd;
    color: white;
    border-radius: 30px;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;
  }
  
  .cta-button:hover {
    background: #0b5ed7;
    transform: translateY(-2px);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .hero-content h1 {
      font-size: 2.5rem;
    }
    
    .tech-grid {
      grid-template-columns: repeat(2, 1fr);
    }
    
    .testimonial-card {
      flex: 0 0 90%;
    }
  }

/* Testimonials Section */
.testimonials {
    padding: 80px 0;
    background: #f9f9f9;
  }
  
  .testimonials .container {
    position: relative;
  }
  
  .testimonial-card {
    max-height: 350px; 
    min-height: 350px; 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
}

.testimonial-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    padding: 20px;
}

.testimonial-content p {
    flex-grow: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 5; 
    -webkit-box-orient: vertical;
}

  
  .testimonial-content h4 {
    font-size: 18px;
    color: #2c3e50;
    margin-bottom: 5px;
  }
  
  .testimonial-content span {
    font-size: 14px;
    color: #777;
  }
  
  /* Swiper Navigation */
  .swiper-button-prev,
  .swiper-button-next {
    color: #0d6efd !important;
    background: rgba(255, 255, 255, 0.9);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .swiper-button-prev::after,
  .swiper-button-next::after {
    font-size: 20px !important;
  }
  
  .swiper-pagination-bullet {
    display: none !important;
    background: #ccc !important;
    opacity: 1 !important;
    margin-top: 10px !important;
  }
  
  .swiper-pagination-bullet-active {
    background: #0d6efd !important;
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .testimonial-card {
      padding: 20px;
    }
  
    .testimonial-content p {
      font-size: 14px;
    }
  
    .testimonial-content h4 {
      font-size: 16px;
    }
  
    .testimonial-content span {
      font-size: 12px;
    }
  }

  /* Custom App Development Section */
.custom-app-section {
    padding: 80px 0;
    background: #e9f2ff;
  }
  
  .section-header {
    text-align: center;
    margin-bottom: 60px;
  }
  
  .section-header h2 {
    font-size: 2.5rem;
    color: #2c3e50;
    margin-bottom: 15px;
  }
  
  .section-header .subtitle {
    color: #666;
    font-size: 1.1rem;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .content-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin: 50px 0;
  }
  
  /* Left Column - Text Content */
  .text-content {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }
  
  .experience-card {
    background: white;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  }
  
  .experience-badge {
    background: #0d6efd;
    color: white;
    padding: 8px 20px;
    border-radius: 20px;
    display: inline-block;
    margin-bottom: 20px;
    font-weight: 600;
  }
  
  .industry-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
    margin-top: 20px;
  }
  
  .industry-item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 15px;
    background: #e9f2ff;
    border-radius: 8px;
    color: #0d6efd;
    font-weight: 500;
  }
  
  .industry-item i {
    font-size: 1.2rem;
  }
  
  .tech-highlight {
    background: white;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  }
  
  .tech-stack {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-top: 20px;
  }
  
  .tech-item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 15px;
    background: #f9f9f9;
    border-radius: 8px;
    transition: transform 0.3s ease;
  }
  
  .tech-item:hover {
    transform: translateY(-5px);
  }
  
  .tech-item img {
    width: 40px;
    height: 40px;
  }
  
  /* Right Column - Visual Content */
  .visual-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  
  .feature-card {
    background: white;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .feature-card:hover {
    transform: translateY(-5px);
  }
  
  .feature-icon3 {
    width: 60px;
    height: 60px;
    background: #0d6efd;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 20px;
    color: white;
    font-size: 1.5rem;
  }
  
  .feature-card h4 {
    font-size: 1.2rem;
    color: #2c3e50;
    margin-bottom: 10px;
  }
  
  .feature-card p {
    color: #666;
    font-size: 0.95rem;
  }
  
  /* CTA Section */
  .cta-wrapper {
    text-align: center;
    margin-top: 60px;
    padding: 40px;
    background: white;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  }
  
  .cta-wrapper h3 {
    font-size: 2rem;
    color: #2c3e50;
    margin-bottom: 15px;
  }
  
  .cta-wrapper p {
    color: #666;
    font-size: 1.1rem;
    margin-bottom: 25px;
  }
  
  .cta-button {
    background: #0d6efd;
    color: white;
    padding: 15px 40px;
    border-radius: 30px;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;
  }
  
  .cta-button:hover {
    background: #0b5ed7;
    transform: translateY(-2px);
  }
  
  /* Responsive Design */
  @media (max-width: 1024px) {
    .content-wrapper {
      grid-template-columns: 1fr;
    }
  
    .visual-content {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 768px) {
    .section-header h2 {
      font-size: 2rem;
    }
  
    .industry-grid,
    .tech-stack,
    .visual-content {
      grid-template-columns: 1fr;
    }
  
    .cta-wrapper h3 {
      font-size: 1.8rem;
    }
  }

  .custom-app-development {
  padding: 60px 0;
  background: #f4f6f8; 
}

