

@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Raleway:ital,wght@0,100..900;1,100..900&family=Sora:wght@100..800&display=swap');

#spinner {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.8s ease-out, visibility 0s linear 0.5s;
  z-index: 99999;
}

#spinner.show {
  transition: opacity 0.8s ease-out, visibility 0s linear 0s;
  visibility: visible;
  opacity: 1;
}


.back-to-top {
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 99;
}
.back-to-top:hover {
  background-color: #007bff !important; 
  opacity: 0.9; 
}

h1,
h2,
h3,
.h1,
.h2,
.h3 {
  font-weight: 400 !important;
  font-family: "Inter", serif !important;
}

h4,
h5,
h6,
.h4,
.h5,
.h6 {
  font-weight: 600 !important;
  font-family: "Inter", serif !important;
}

.my-6 {
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.py-6 {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.wow,
.animated {
  animation-duration: 2s !important;
}

.btn {
  font-family: "Nunito", sans-serif;
  font-weight: 600;
  transition: 0.5s;
}

.btn-primary,
.btn-secondary {
  color: #ffffff;
  box-shadow: inset 0 0 0 50px transparent;
}

.btn-primary:hover {
  box-shadow: inset 0 0 0 0 var(--primary);
}

.btn-secondary:hover {
  box-shadow: inset 0 0 0 0 var(--secondary);
}

.btn-square {
  width: 36px;
  height: 36px;
}

.btn-sm-square {
  width: 30px;
  height: 30px;
}

.btn-lg-square {
  width: 48px;
  height: 48px;
}

.btn-square,
.btn-sm-square,
.btn-lg-square {
  padding-left: 0;
  padding-right: 0;
  text-align: center;
}


.nav-bar {

  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.navbar .navbar-nav .nav-link {
  padding: 10px 12px;
  font-weight: 600;
  font-size: 17px;
  transition: 0.5s;
}

.navbar .navbar-nav .nav-link:hover,
.navbar .navbar-nav .nav-link.active {
  color: var(--bs-primary);
}

.navbar .dropdown-toggle::after {
  border: none;
  content: "\f107";
  font-family: "Font Awesome 5 Free";
  font-weight: 700;
  vertical-align: middle;
  margin-left: 8px;
}

@media (min-width: 1200px) {
  .navbar .nav-item .dropdown-menu {
    display: block;
    visibility: hidden;
    top: 100%;
    transform: rotateX(-75deg);
    transform-origin: 0% 0%;
    transition: 0.5s;
    opacity: 0;
  }

  .navbar .nav-item:hover .dropdown-menu {
    transform: rotateX(0deg);
    visibility: visible;
    opacity: 1;
  }
}
.link-animated a {
  transition: 0.5s;
}

.link-animated a:hover {
  padding-left: 10px;
}

.about-img {
  position: relative;
  overflow: hidden;
}

.about-img::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  
  background-size: contain;
}
.feature {
  
  background-size: cover;
}

.section-title::before {
  position: absolute;
  content: "";
  width: 150px;
  height: 5px;
  left: 0;
  bottom: 0;
  background: var(--primary);
  border-radius: 2px;
}

.section-title.text-center::before {
  left: 50%;
  margin-left: -75px;
}

.section-title.section-title-sm::before {
  width: 90px;
  height: 3px;
}

.section-title::after {
  position: absolute;
  content: "";
  width: 6px;
  height: 5px;
  bottom: 0px;
  background: #ffffff;
  -webkit-animation: section-title-run 5s infinite linear;
  animation: section-title-run 5s infinite linear;
}

.section-title.section-title-sm::after {
  width: 4px;
  height: 3px;
}

.section-title.text-center::after {
  -webkit-animation: section-title-run-center 5s infinite linear;
  animation: section-title-run-center 5s infinite linear;
}

.section-title.section-title-sm::after {
  -webkit-animation: section-title-run-sm 5s infinite linear;
  animation: section-title-run-sm 5s infinite linear;
}

@-webkit-keyframes section-title-run {
  0% {
    left: 0;
  }
  50% {
    left: 145px;
  }
  100% {
    left: 0;
  }
}

@-webkit-keyframes section-title-run-center {
  0% {
    left: 50%;
    margin-left: -75px;
  }
  50% {
    left: 50%;
    margin-left: 45px;
  }
  100% {
    left: 50%;
    margin-left: -75px;
  }
}

@-webkit-keyframes section-title-run-sm {
  0% {
    left: 0;
  }
  50% {
    left: 85px;
  }
  100% {
    left: 0;
  }
}

.dropdown .dropdown-menu a:hover {
  background: var(--bs-primary);
  color: var(--bs-white);
}

#searchModal .modal-content {
  background-color: rgba(255, 255, 255, 0.95);
}

.event .tab-class .nav-item a.active {
  background: var(--bs-primary) !important;
}

.event .event-img .event-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  background: skyblue;
  border-radius: 8px;
  transition: 0.5s;
  opacity: 0;
  z-index: 1;
}

.event .event-img:hover .event-overlay {
  opacity: 1;
}

.service .service-item {
  position: relative;
  height: 100%;
  border-radius: 8px;
  box-shadow: 0 0 45px rgba(0, 0, 0, 0.08);
}

.service-content::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  bottom: auto;
  background: var(--bs-primary);
  border-radius: 8px;
  transition: 1s;
}

.service-item:hover .service-content::after {
  height: 100%;
  opacity: 1;
}

.service-item .service-content-icon {
  position: relative;
  z-index: 2;
}

.service-item .service-content-icon i,
.service-item .service-content-icon p {
  transition: 1s;
}

.service-item:hover .service-content-icon i {
  color: var(--bs-dark) !important;
}

.service-item:hover .service-content-icon p {
  color: var(--bs-white);
}

.service-item:hover .service-content-icon a.btn-primary {
  background: var(--bs-white);
  color: var(--bs-dark);
}

.service-item .service-content-icon a.btn-primary {
  transition: 1s !important;
}

.menu .nav-item a.active {
  background: var(--bs-primary) !important;
}

.menu .menu-item .border-bottom {
  border-bottom-style: dashed !important;
}

.video {
  position: relative;
  height: 100%;
  min-height: 400px;

  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 12px;
}

.video .btn-play {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  box-sizing: content-box;
  display: block;
  width: 32px;
  height: 44px;
  border-radius: 50%;
  border: none;
  outline: none;
  padding: 18px 20px 18px 28px;
}

.video .btn-play:before {
  content: "";
  position: absolute;
  z-index: 0;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: block;
  width: 100px;
  height: 100px;
  background: var(--bs-primary);
  border-radius: 50%;
  animation: pulse-border 1500ms ease-out infinite;
}

.video .btn-play:after {
  content: "";
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: block;
  width: 100px;
  height: 100px;
  background: var(--bs-white);
  border-radius: 50%;
  transition: all 200ms;
}

.video .btn-play img {
  position: relative;
  z-index: 3;
  max-width: 100%;
  width: auto;
  height: auto;
}

.video .btn-play span {
  display: block;
  position: relative;
  z-index: 3;
  width: 0;
  height: 0;
  border-left: 32px solid var(--bs-dark);
  border-top: 22px solid transparent;
  border-bottom: 22px solid transparent;
}

@keyframes pulse-border {
  0% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
    opacity: 1;
  }

  100% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
    opacity: 0;
  }
}

#videoModal {
  z-index: 99999;
}

#videoModal .modal-dialog {
  position: relative;
  max-width: 800px;
  margin: 60px auto 0 auto;
}

#videoModal .modal-body {
  position: relative;
  padding: 0px;
}

#videoModal .close {
  position: absolute;
  width: 30px;
  height: 30px;
  right: 0px;
  top: -30px;
  z-index: 999;
  font-size: 30px;
  font-weight: normal;
  color: #ffffff;
  background: #000000;
  opacity: 1;
}

.blog-item {
  position: relative;
}

.blog-item img {
  transition: 0.5s;
}

.blog-item:hover img {
  transform: scale(1.3);
}

.blog-item .blog-content {
  position: relative;
  transform: translateY(-50%);
  box-shadow: 0 0 45px rgba(0, 0, 0, 0.08);
}

.blog-item .blog-content a.btn h5 {
  transition: 0.5s;
}

.blog-item:hover .blog-content a.btn h5 {
  color: var(--bs-primary) !important;
}

.team-item {
  width: 100%;
  height: 100%;
  position: relative;
}

.team-item .team-icon {
  position: absolute;
  top: 0;
  right: 0;
}

.team-item .team-icon .share-link {
  opacity: 0;
  transition: 0.9s;
}

.team-item:hover .share-link {
  opacity: 1;
}

.team-item .team-content {
  transition: 0.9s;
}

.team-item:hover .team-content {

  background: #4786e6 !important;
  color: var(--bs-dark) !important;
}

.team-item .team-content h4,
.team-item .team-content p {
  transition: 0.5s;
}

.team-item:hover .team-content h4 {
  color: var(--bs-dark) !important;
}

.team-item:hover .team-content p {
  color: var(--bs-white);
}

.testimonial-item {

  border-radius: 12px;
  padding: 20px 20px;
}

.testimonial-carousel .owl-item img {
  width: 100px;
  height: 100px;
}

.testimonial-carousel.owl-rtl .testimonial-item {
  direction: ltr !important;
}

.team-item {
  transition: 0.5s;
} 



.team-item:hover {
  box-shadow: 0 0 30px #dddddd;
} 



.team-item:hover .team-social a.btn:first-child {
  opacity: 1;
  margin-top: 0;
  transition: 0.3s 0s;
} 





.team-item:hover .team-img img,
.blog-item:hover .blog-img img {
  transform: scale(1.15);
}
.contact-form {
  box-shadow: 0 0 45px rgba(0, 0, 0, 0.08);
}


.footer .footer-item a.text-body:hover {
  color: var(--bs-primary) !important;
}

  @media (max-width: 768px) {
    .team-item {
      padding: 1rem;
    }
    .bounceInUp{
      margin-bottom: 50px !important;
      margin-top: 40px !important;
    }

  }


@keyframes img-up-down {
  0% {
    left: 0px;
    bottom: 25px;
}

100% {
    left: 0px;
    bottom: 10px;
}
}

