/* CloudMigration.css */
.cloud-migration {
  font-family: 'Inter', sans-serif;
  color: #2c3e50;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Hero Section */
.migration-hero {
  background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
  padding: 120px 0;
  text-align: center;
}

.migration-hero h1 {
  font-size: 3.5rem;
  margin-bottom: 20px;
}

.migration-hero .lead {
  font-size: 1.25rem;
  margin-bottom: 40px;
  opacity: 0.9;
}


/* partenering section  */

.assImg1:hover{
  background: #c2d4ff;
  border: #c2d4ff;
}




/* benifite section  */

.benefits-section1 {
  padding: 80px 0;
  /* background: #fff ; */
  background: #f9fafb;
}

.section-title {
  font-size: 36px;
  font-weight: 700;
  /* text-align: center; */
  color: #333;
  margin-bottom: 60px;
}

.benefits-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.benefit-item {
  padding: 20px;
  background: transparent;
  color: #333;
  text-align: center;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.benefit-item:hover {
  transform: translateY(-5px);
}

.benefit-icon1 {
  font-size: 3rem;
  color: #0d6efd;
  margin-bottom: 20px;
}

.benefit-item h3 {
  font-size: 24px;
  font-weight: 600;
  color: #333;
  margin-bottom: 15px;
}

.benefit-item p {
  color: #777;
  font-size: 16px;
  line-height: 1.5;
  max-width: 320px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .benefits-grid {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .benefits-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}


/* Process Section */
.process-section {
  padding: 80px 0;
}

.process-timeline {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 40px;
}

.process-step {
  padding: 30px;
  border-left: 3px solid #0d6efd;
  position: relative;
}

.step-number {
  font-size: 1.5rem;
  font-weight: 700;
  color: #0d6efd;
  margin-bottom: 15px;
}

/* Tech Section */
.tech-section {
  padding: 80px 0;
  background: #fff;
}

.platform-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.platform-card {
  text-align: center;
  padding: 30px;
  background: white;
  border-radius: 10px;
  border: 1px solid #e2e8f0;
}

.platform-card img {
  height: 80px;
  margin-bottom: 20px;
}

.platform-stats span {
  font-size: 2rem;
  font-weight: 700;
  color: #0d6efd;
  display: block;
  margin: 10px 0;
}

/* CTA Section */
.cta-section5 {
  padding: 80px 0;
  background: #f9fafb;
  /* color: white; */
  text-align: center;
}

/* .cta-button {

  padding: 15px 40px;
  border-radius: 30px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
} */

.cta-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .benefits-grid,
  .process-timeline,
  .platform-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .migration-hero h1 {
    font-size: 2.5rem;
  }

  .benefits-grid,
  .process-timeline,
  .platform-grid {
    grid-template-columns: 1fr;
  }
  
  .process-step {
    border-left: none;
    border-bottom: 3px solid #0d6efd;
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
}

