/* Base Styles */
.poc-development {
    font-family: 'Inter', sans-serif;
    color: #2c3e50;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  /* Hero Section */
  .poc-hero {
    /* background: linear-gradient(135deg, #0d6efd, #0252d1); */
    background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
    color: white;
    padding: 120px 0;
    text-align: center;
  }
  
  .poc-hero h1 {
    font-size: 3.5rem;
    margin-bottom: 20px;
  }
  
  .poc-hero p {
    font-size: 1.25rem;
    margin-bottom: 40px;
  }
  
  .cta-group {
    display: flex;
    gap: 20px;
    justify-content: center;
  }
  
  .cta-primary, .cta-secondary {
    padding: 15px 40px;
    border-radius: 30px;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;
  }
  
  .cta-primary {
    background: white;
    color: #0d6efd;
  }
  
  .cta-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  }
  
  .cta-secondary {
    border: 2px solid white;
    color: white;
  }
  
  .cta-secondary:hover {
    background: white;
    color: #0d6efd;
  }
  
  /* Value Proposition */
  .value-proposition1 {
    padding: 80px 0;
    margin-top: 40px;
  }
  
  .value-card {
    background: white;
    border-radius: 15px;
    padding: 40px;
    box-shadow: 0 10px 30px rgba(0,0,0,0.1);
    margin-top: -80px;
    position: relative;
    z-index: 1;
  }
  
  .value-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-top: 40px;
  }
  
  .value-item {
    text-align: center;
    padding: 30px;
    border-radius: 10px;
    transition: transform 0.3s ease;
  }
  
  .value-item:hover {
    transform: translateY(-5px);
  }
  
  .value-icon {
    width: 60px;
    height: 60px;
    background: #0d6efd;
    border-radius: 50%;
    margin: 0 auto 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1.5rem;
  }
  
  /* Process Section */
  .process-section5 {
    text-align: center;
    padding: 80px 0;
    background: #f9fafb;
  }
  
  .process-timeline {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
    margin-top: 40px;
  }
  
  .process-step {
    background: white;
    border-radius: 10px;
    padding: 30px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.05);
    position: relative;
  }
  
  .step-number {
    font-size: 2rem;
    font-weight: 700;
    color: #0d6efd;
    margin-bottom: 15px;
  }
  
  .step-content i {
    font-size: 2rem;
    color: #0d6efd;
    margin-bottom: 15px;
  }
  
/* Case Studies */
.case-studies6 {
  text-align: center;
    padding: 80px 0;
}

.case-study-card {
    border-radius: 10px;
    overflow: hidden;
    background: white;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    text-align: center;
}

.case-study-card:hover {
    transform: translateY(-5px);
}

.case-study-image {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.case-study-content {
    padding: 20px;
}

.case-study-content h3 {
    font-size: 1.2rem;
    margin-bottom: 10px;
}

.result-badge {
    background: #0d6efd;
    color: white;
    padding: 5px 15px;
    border-radius: 20px;
    display: inline-block;
    font-size: 0.9rem;
    font-weight: bold;
    margin-top: 10px;
}

  
  /* Benefits Section */
  .benefits-section {
    padding: 80px 0;
    background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
    color: white;
  }
  
  .benefits-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-top: 40px;
  }
  
  .benefit-card {
    background: white;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
  }
  
  .benefit-icon {
    font-size: 2rem;
    margin-bottom: 15px;
  }
  
  /* CTA Section */
  .poc-cta {
    padding: 80px 0;
    text-align: center;
  }
  
  .cta-content {
    max-width: 800px;
    margin: 0 auto;
  }
  
  /* Responsive Design */
  @media (max-width: 1024px) {
    .process-timeline {
      grid-template-columns: repeat(2, 1fr);
    }
  
    .value-grid {
      grid-template-columns: 1fr;
    }
  }
  
  @media (max-width: 768px) {
    .poc-hero h1 {
      font-size: 2.5rem;
    }
  
    .process-timeline,
    .benefits-grid {
      grid-template-columns: 1fr;
    }
  
    .cta-group {
      flex-direction: column;
      align-items: center;
    }
  }