/* Base Styles */
.app-reengineering {
    font-family: 'Inter', sans-serif;
    color: #2c3e50;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  /* Hero Section */
  .hero {
    background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
    padding: 120px 0;
    text-align: center;
  }
  
  .hero h1 {
    font-size: 3.5rem;
    margin-bottom: 20px;
  }
  
  .hero .lead {
    font-size: 1.25rem;
    margin-bottom: 40px;
    opacity: 0.9;
  }
  
  .cta-button {
    background: #0d6efd;
    color: white;
    padding: 15px 40px;
    border-radius: 30px;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;
    display: inline-block;
  }
  
  .cta-button:hover {
    background: #0b5ed7;
    transform: translateY(-2px);
  }
  
  /* Benefits Section */
  .benefits-section {
    padding: 80px 0;
    background: #f9fafb;
  }
  
  .benefits-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-top: 40px;
  }
  
  .benefit-card {
    /* background: white; */
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .benefit-card:hover {
    transform: translateY(-5px);
  }
  
  .benefit-icon {
    font-size: 2rem;
    color: #0d6efd;
    margin-bottom: 20px;
  }
  
  .stat-badge {
    background: #e9f2ff;
    color: #0d6efd;
    padding: 8px 20px;
    border-radius: 20px;
    display: inline-block;
    margin-top: 20px;
    font-weight: 600;
  }
  
  /* Process Section */
  .process-section {
    padding: 80px 0;
  }
  
  .process-timeline {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    margin-top: 40px;
  }
  
  .process-step {
    background: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    text-align: center;
  }
  
  .step-number {
    font-size: 2rem;
    font-weight: 700;
    color: #0d6efd;
    margin-bottom: 15px;
  }
  
  /* Case Studies */
  .case-studies {
    padding: 80px 0;
    background: #f9fafb;
  }
  
  .case-study {
    background: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    text-align: center;
  }
  
  .case-header h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .case-stats {
    display: flex;
    gap: 20px;
    margin-top: 20px;
  }
  
  .stat {
    flex: 1;
    background: #f9fafb;
    padding: 15px;
    border-radius: 10px;
  }
  
  .stat span {
    font-size: 1.5rem;
    font-weight: 700;
    color: #0d6efd;
  }
  
  /* CTA Section */
  .cta-section {
    padding: 80px 0;
    background: #fff !important;
    /* color: white; */
    text-align: center;
  }
  
  .cta-section h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .cta-section p {
    font-size: 1.25rem;
    margin-bottom: 40px;
    color: black;
  }
  
  /* Responsive Design */
  @media (max-width: 1024px) {
    .benefits-grid,
    .process-timeline {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 768px) {
    .hero h1 {
      font-size: 2.5rem;
    }
  
    .benefits-grid,
    .process-timeline {
      grid-template-columns: 1fr;
    }
  }