.scada-iot-page .hero-section {
  background-color: #f0f8ff;
  padding: 60px 0;
  text-align: center;
}

.scada-iot-page .overview,
.scada-iot-page .technologies,
.scada-iot-page .case-studies,
.scada-iot-page .cta {
  padding: 50px 0;
}

.scada-iot-page .technologies {
  background-color: #f9f9f9;
}

.scada-iot-page .technologies ul {
  list-style-type: none;
  padding-left: 0;
}

.scada-iot-page .technologies li {
  font-size: 1.1rem;
  margin-bottom: 15px;
}

.scada-iot-page .case-studies .card {
  margin-bottom: 30px;
}

.scada-iot-page .card-img-top {
  max-height: 200px;
  object-fit: cover;
}

.scada-iot-page .cta {
  background-color: #007bff;
  color: white;
}

.scada-iot-page .cta a {
  font-size: 1.2rem;
  padding: 15px 30px;
  background-color: #28a745;
  color: #fff;
  border-radius: 5px;
  text-decoration: none;
}

.scada-iot-page .cta a:hover {
  background-color: #218838;
}
