.imgData {
  height: 350px;
  width: 300px;
  float: right;
}

.cardsBlock {
  margin: 0;
  padding: 30px 0 30px 0;
  width: 100% !important;
  background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
}

.disbaleBtn {
  background: gray;
  color: white;
}


.career-section {
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

.career-badge {
  display: inline-block;
  padding: 8px 20px;
  background: linear-gradient(135deg, #4786e6 0%, #2a5eb1 100%);
  color: white;
  border-radius: 30px;
  font-weight: 600;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 15px rgba(71, 134, 230, 0.2);
}

.future-text {
  background: linear-gradient(135deg, #A3C2F1 0%, #286FE1 100%);
  /* background: linear-gradient(90deg, #f57e20, #4786e6); */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
  margin-left: 8px;
}

.value-icon {
  width: 40px;
  height: 40px;
  background: #4786e6;
  color: white;
  border-radius: 10px;
  text-align: center;
  line-height: 40px;
  margin-right: 15px;
  flex-shrink: 0;
}

.value-icon5 {
  width: 40px;
  height: 40px;
  background: #4786e6;
  color: white;
  border-radius: 10px;
  text-align: center;
  line-height: 40px;
  margin-right: 15px;
  flex-shrink: 0;
}

.cta-button {
  background: linear-gradient(135deg, #4786e6 0%, #2a5eb1 100%);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.cta-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 20px rgba(71, 134, 230, 0.3);
}

.career-image-container {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  transform: perspective(1000px) rotateY(-5deg);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.1);
}

.main-image {
  position: relative;
  z-index: 2;
  border-radius: 20px;
}

.gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(71, 134, 230, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
  z-index: 1;
}

.floating-element {
  position: absolute;
  background: rgba(71, 134, 230, 0.1);
  border-radius: 15px;
}

.element-1 {
  width: 80px;
  height: 80px;
  top: -20px;
  left: -20px;
  animation: float 4s infinite;
}

.element-2 {
  width: 60px;
  height: 60px;
  bottom: -20px;
  right: -20px;
  animation: float 4s infinite 0.5s;
}

@keyframes float {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-15px);
  }

  100% {
    transform: translateY(0);
  }
}

@media (max-width: 992px) {
  .career-image-container {
    transform: none;
    margin-top: 40px;
  }
}


.shadow-hover {
  transition: all 0.3s ease;
  border: 1px solid rgba(71, 134, 230, 0.1);
  border-radius: 12px;
}

.shadow-hover:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(71, 134, 230, 0.1);
}

.btn-primary-gradient {
  background: linear-gradient(135deg, #4786e6 0%, #2a5eb1 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.btn-primary-gradient:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(71, 134, 230, 0.3);
}

.pagination .page-item.active .page-link {
  background-color: #4786e6;
  border-color: #4786e6;
}

.pagination .page-link {
  color: #4786e6;
  margin: 0 5px;
  border-radius: 8px;
}

.pagination .page-link:hover {
  background-color: #f8f9fa;
}