.facts-container {
  position: relative;
  background: #f9fbfd;
  overflow: hidden;
}

.fact-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(71, 134, 230, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.fact-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(71, 134, 230, 0.15);
}

.fact-icon {
  width: 60px;
  height: 60px;
  background: #bbd6ff;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1.5rem;
  transition: all 0.3s ease;
}

.fact-icon .icon {
  color: white;
  font-size: 28px;
}

.fact-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a365d;
  margin: 0.5rem 0;
  font-family: 'Inter', sans-serif;
}

.fact-label {
  color: #4a5568;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin: 0;
}

.video-container {
  position: relative;
  border-radius: 16px;
  overflow: hidden;
  transform: perspective(1000px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
}

.promo-video {
  width: 100%;
  height: auto;
  border-radius: 16px;
  display: block;
}

.video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(71, 134, 230, 0.1) 0%, rgba(255,255,255,0) 100%);
}

/* Responsive Design */
@media (max-width: 992px) {
  .facts-container {
    padding: 4rem 0;
  }
  
  .fact-card {
    margin-bottom: 2rem;
    padding: 1.5rem;
  }
  
  .fact-number {
    font-size: 2rem;
  }
  
  .video-container {
    margin-top: 3rem;
  }
}

@media (max-width: 768px) {
  .fact-icon {
    width: 50px;
    height: 50px;
  }
  
  .fact-icon .icon {
    font-size: 24px;
  }
  
  .fact-label {
    font-size: 0.875rem;
  }
}

@media (max-width: 576px) {
  .fact-card {
    padding: 1.25rem;
  }
  
  .fact-number {
    font-size: 1.75rem;
  }
}