.admin-blogs-approve {
  background-color: #f8f9fa;
  min-height: 100vh;
}

.admin-blogs-header {
  background-color: #0d6efd;
  padding: 2rem 0;
}

.card {
  border: none;
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.blog-image {
  height: 200px;
  object-fit: cover;
}

.card-title {
  font-size: 1.25rem;
  font-weight: 600;
}

.card-text {
  color: #4a4a4a;
  line-height: 1.6;
}

.badge {
  font-size: 0.875rem;
  font-weight: 500;
  padding: 0.25rem 0.5rem;
}

.btn-sm {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
}

@media (max-width: 768px) {
  .blog-image {
    height: 150px;
  }
}