/* Base Styles */
.enterprise-applications {
    font-family: 'Inter', sans-serif;
    color: #2c3e50;
  }
  
  /* Hero Section */
  .enterprise-hero {
    /* background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
      url('../assets/enterprise/hero-bg.jpg') center/cover; */
      background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
    color: white;
    padding: 120px 0;
    text-align: center;
  }
  
  .enterprise-hero h1 {
    font-size: 3.5rem;
    margin-bottom: 20px;
  }
  
  .enterprise-hero .lead {
    font-size: 1.25rem;
    margin-bottom: 40px;
    opacity: 0.9;
  }
  
  /* Solutions Section */
  .solutions-section {
    padding: 80px 0;
    background: #f9fafb;
  }
  
  .solution-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
  
  .solution-card {
    background: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease;
  }
  
  .solution-card:hover {
    transform: translateY(-10px);
  }
  
  .solution-header {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 20px;
  }
  
  .solution-header i {
    font-size: 2rem;
    color: #0d6efd;
  }
  
  .solution-features {
    list-style: none;
    padding: 0;
    margin: 20px 0;
  }
  
  .solution-features li {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
  }
  
  .solution-cta {
    color: #0d6efd;
    text-decoration: none;
    font-weight: 600;
    display: block;
    margin-top: 20px;
  }
  
  
/* Benefits Section */
.benefits-section {
  padding: 80px 0;
  background: linear-gradient(90deg, rgba(228, 237, 255, 0.4) 0%, rgba(64, 124, 238, 0.4) 100%);
  text-align: center;
}

.section-title1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

/* Grid Layout */
.benefits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

/* Benefits Section */
.benefits-section2 {
  padding: 100px 0;
  background: #fff;
  text-align: center;
}

.section-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #1d3557;
  margin-bottom: 15px;
}

.section-subtitle {
  font-size: 1.2rem;
  color: #457b9d;
  margin-bottom: 40px;
}

/* Benefits Grid */
.benefits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Benefit Card */
.benefit-card {
  position: relative;
  padding: 30px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 100%;
  min-height: 320px; /* Ensures all cards have the same height */
}

.benefit-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

/* Overlay Effect */
.benefit-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(13, 110, 253, 0.1), rgba(0, 176, 255, 0.1));
  border-radius: 15px;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.benefit-card:hover .benefit-overlay {
  opacity: 1;
}

/* Icon */
.benefit-icon {
  width: 80px;
  height: 80px;
  background: rgba(0, 176, 255, 0.1);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}

.benefit-icon img {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

/* Consistent Title (2 lines) */
.benefit-content h3 {
  font-size: 20px;
  font-weight: 600;
  color: #2c3e50;
  text-align: center;
  margin-bottom: 10px;
  
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 48px; /* Ensures consistent height */
}

/* Consistent Description (3 lines) */
.benefit-content p {
  font-size: 16px;
  color: #555;
  text-align: center;
  
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 72px; /* Ensures consistent height */
}


/* Content */
.benefit-content h3 {
  font-size: 1.4rem;
  color: #1d3557;
  font-weight: 600;
  margin-bottom: 10px;
}

.benefit-content p {
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 768px) {
  .benefits-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }

  .section-title {
    font-size: 2rem;
  }
}


/* Responsive Design */
@media (max-width: 768px) {
  .benefits-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}

  
/* Case Studies */
.case-studies {
  padding: 80px 0;
  background: #f9fafb;
}

.case-study-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  height: 100%;
  min-height: 420px; /* Ensures all cards have the same height */
}

.case-study-card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.case-content {
  flex-grow: 1;
  padding: 25px;
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Ensuring all titles take 2 lines */
.case-content h3 {
  font-size: 20px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 10px;
  text-align: center;
  
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 48px; /* Ensures consistent height */
}

/* Ensuring all descriptions take 3 lines */
.result {
  color: #0d6efd;
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 72px; /* Ensures consistent height */
}

.study-link {
  color: #2c3e50;
  text-decoration: none;
  font-weight: 600;
  text-align: center;
}

.study-link:hover {
  color: #0d6efd;
}

  
  /* CTA Section */
  .enterprise-cta {
    padding: 80px 0;
    /* background: #2c3e50; */
    /* color: white; */
    text-align: center;
  }
  
  /* Responsive Design */
  @media (max-width: 1024px) {
    .solution-cards,
    .value-grid,
    .benefits-grid {
      grid-template-columns: 1fr;
    }
  }
  
  @media (max-width: 768px) {
    .enterprise-hero h1 {
      font-size: 2.5rem;
    }
  
    .cta-group {
      flex-direction: column;
      gap: 15px;
    }
  }