/* Services.css */
.service-card {
  background: #fff;
  border-radius: 15px;
  padding: 30px;
  text-align: center;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(71, 134, 230, 0.1);
  height: 320px;
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(71, 134, 230, 0.2);
}

.service-icon {
  width: 100px;
  height: 100px;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #4786e6, #2a5eb1);
  border-radius: 50%;
  transition: all 0.4s ease;
}

.service-icon img {
  width: 50px;
  height: auto;
  filter: brightness(0) invert(1);
}

.service-card:hover .service-icon {
  transform: scale(1.1);
  background: linear-gradient(135deg, #2a5eb1, #4786e6);
}

.service-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2a2a2a;
  margin-bottom: 15px;
  transition: all 0.4s ease;
}

.service-description {
  font-size: 0.95rem;
  color: #7f8c8d;
  line-height: 1.6;
  margin-bottom: 0;
  transition: all 0.4s ease;
}

.service-hover-content {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #4786e6, #2a5eb1);
  border-radius: 15px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: all 0.4s ease;
}

.service-card:hover .service-hover-content {
  top: 0;
  opacity: 1;
}

.hover-description {
  font-size: 0.95rem;
  color: #fff;
  text-align: center;
  margin-bottom: 20px;
}

.service-button {
  background: #fff;
  color: #4786e6;
  border: none;
  padding: 10px 20px;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.service-button:hover {
  background: #2a5eb1;
  color: #fff;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Responsive Styles */
@media (max-width: 992px) {
  .service-card {
    margin-bottom: 20px;
  }

  .service-title {
    font-size: 1.3rem;
  }

  .service-description {
    font-size: 0.9rem;
  }
}

@media (max-width: 768px) {
  .service-card {
    padding: 20px;
  }

  .service-icon {
    width: 80px;
    height: 80px;
  }

  .service-icon img {
    width: 40px;
  }

  .service-title {
    font-size: 1.2rem;
  }

  .service-description {
    font-size: 0.85rem;
  }
}