/* Base Styles */
.product-delivery {
  font-family: 'Inter', sans-serif;
  color: #2c3e50;
}

/* Hero Section */
.delivery-hero {
  background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
  color: white;
  padding: 120px 0;
  text-align: center;
}

.delivery-hero h1 {
  font-size: 3.5rem;
  margin-bottom: 20px;
}

.delivery-hero p {
  font-size: 1.25rem;
  margin-bottom: 40px;
  opacity: 0.9;
}

.cta-button {
  background: #0d6efd;
  color: white;
  padding: 15px 40px;
  border-radius: 30px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.cta-button:hover {
  background: #0b5ed7;
  transform: translateY(-2px);
}

/* Value Metrics */
.value-metrics {
  padding: 80px 0;
  background: #f9fafb;
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.metric-card2 {
  background: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  text-align: center;
  transition: transform 0.3s ease;
}

.metric-card2:hover {
  transform: translateY(-5px);
}

.metric-icon {
  font-size: 2.5rem;
  /* color: #0d6efd; */
  margin-bottom: 20px;
}

/* Features Section */
.features-section {
  padding: 80px 0;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.feature-card {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-icon2 {
  font-size: 2rem;
  color: #0d6efd;
  margin-bottom: 20px;
}

.feature-description {
  color: #666;
  font-size: 0.95rem;
  margin-top: 10px;
}

/* Tech Stack */
.tech-stack-section {
  padding: 80px 0;
  background: #f9fafb;
}

.tech-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.tech-card {
  background: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tech-card:hover {
  transform: translateY(-5px);
}

.tech-card img {
  height: 110px;
  width: 120px;
  margin-bottom: 15px;
}

/* Case Studies */
.case-studies {
  padding: 80px 0;
  background-color: #f8f9fa;
}

.case-studies h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 40px;
  color: #2c3e50;
  text-align: center;
}

.case-study-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  min-height: 420px; /* Ensures all cards have the same height */
  border-radius: 12px;
  overflow: hidden;
  background: white;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.case-study-card:hover {
  transform: translateY(-5px);
}

.case-study-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-bottom: 3px solid #0d6efd;
}

.case-content {
  flex-grow: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.case-content h3 {
  font-size: 20px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 8px;
  /* Ensuring all titles take 2 lines */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 48px; /* Ensures consistent height */
}

.result {
  color: #0d6efd;
  font-weight: 600;
  font-size: 16px;
  margin: 8px 0;
  /* Ensuring all descriptions take 3 lines */
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 72px; /* Ensures consistent height */
}

.study-link {
  color: #2c3e50;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
}

.study-link:hover {
  color: #0d6efd;
}

.swiper{
  padding: 20px !important;
}

/* Benefits Section */
.benefits-section3 {
  padding: 80px 0;
  text-align: center;
}

.benefits-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.benefit-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  height: 100%;
  min-height: 350px; /* Ensures all cards have the same height */
}

.benefit-icon {
  font-size: 2rem;
  margin-bottom: 15px;
  color: blue;
}

/* Ensuring all titles take 2 lines */
.benefit-card h3 {
  font-size: 20px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 8px;
  text-align: center;
  
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 48px; /* Ensures consistent height */
}

/* Ensuring all descriptions take 3 lines */
.benefit-card .lead {
  font-size: 16px;
  color: #555;
  
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 72px; /* Ensures consistent height */
}


/* CTA Section */
.delivery-cta {
  padding: 80px 0;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .metrics-grid,
  .features-grid,
  .benefits-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .tech-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .delivery-hero h1 {
    font-size: 2.5rem;
  }

  .metrics-grid,
  .features-grid,
  .benefits-grid,
  .tech-grid {
    grid-template-columns: 1fr;
  }
}