.tech-container {
  margin-top: 150px;
  width: 100%;
  height: 100%;
}

.box-1,
.box-2 {
  display: flex;
  /* flex-direction: row; */
  /* justify-content: space-around; */
  align-items: center;
}


.circle-tech {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 400px;
  /* background: url(../assets/img/circle.png) no-repeat center; */
  /* background: url(../assets/img/circle-2.png) no-repeat center; */
  background: url(../assets/img/circle-3.png) no-repeat center;
  background-size: cover;
  /* border-radius: 50%; */
  position: relative;
}

.circle img {
  height: 75%;
}

.circleproject-tech {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}


.circleproject-item-tech {
  position: absolute;
  width: 50px;
  height: 50px;
  transform-origin: center center;

  /* transition: transform 1s ease; Optional: for smoother transition */
  /* animation: rotate 10s linear infinite; Adjust duration as needed */
}

.circleproject-tech div {
  display: block;
  position: absolute;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100px;
  height: 100px;
  /* margin: -20%; */
}

.circleproject-tech img {
  display: block;
  width: 100%;
  height: 100%;
}
