      #end-poverty { transform: rotate(45deg) translate(100px) rotate(-45deg); }
      #ananda { transform: rotate(90deg) translate(100px) rotate(-90deg); } */

/* Rotations based on your angles with counter-rotation */
#item-0 {
  transform: rotate(0deg) translate(190px) rotate(0deg); /* 0 degrees */
}

#item-1 {
  transform: rotate(120deg) translate(190px) rotate(-120deg); /* 45 degrees */
}

#item-2 {
  transform: rotate(240deg) translate(190px) rotate(-240deg); /* 90 degrees */
}

#item-3 {
  transform: rotate(135deg) translate(190px) rotate(-135deg); /* 135 degrees */
}

#item-4 {
  transform: rotate(180deg) translate(190px) rotate(-180deg); /* 180 degrees */
}

#item-5 {
  transform: rotate(225deg) translate(190px) rotate(-225deg); /* 225 degrees */
}

#item-6 {
  transform: rotate(270deg) translate(190px) rotate(-270deg); /* 270 degrees */
}

#item-7 {
  transform: rotate(315deg) translate(190px) rotate(-315deg); /* 315 degrees */
}

#item-8 {
  transform: rotate(360deg) translate(190px) rotate(-360deg); /* 360 degrees */
}
