.blob {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(136.5deg, rgba(228, 237, 255, 0.5) 4.15%, rgba(71, 134, 230, 0.5) 63.56%);

  z-index: -1; /* Place behind other elements */
}

.blob1 {
  width: 150px;
  height: 150px;
  top: 50px;
  left: 50px;
}

.blob2 {
  width: 100px;
  height: 100px;
  bottom: 50px;
  right: 100px;
}

.blob3 {
  width: 120px;
  height: 120px;
  top: 200px;
  right: 50px;
}
