/* Hero Section */
.project-hero {
    background-size: cover;
    background-position: center;
    padding: 80px 0;
    color: white;
    position: relative;
    background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);
  }
  
  .hero-title {
    font-size: 2.8rem;
    font-weight: 700;
    margin-bottom: 20px;
    /* display: block; */
    /* line-height: 20px; */
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    color: black;
  }

  .project-details{
    padding: 80px 0;
  }
  
  /* General Sections */
  .section-title {
    font-size: 2.2rem;
    font-weight: 600;
    /* color: #2a5eb1; */
    margin-bottom: 2.5rem;
    position: relative;
    padding-bottom: 0.5rem;
  }
  
  .section-title::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 3px;
    background: #4786e6;
  }
  
  
  /* Key Features */

  .key-features-section{
    padding: 80px 0;
  }

  .featured-image9{
    height: 500px;
    width: 500px;
    overflow: hidden;
    object-fit: contain;
  }

  .feature-card {
    background: #ffffff;
    border-radius: 12px;
    padding: 2rem;
    margin-bottom: 1.5rem;
    box-shadow: 0 10px 30px rgba(0,0,0,0.08);
    transition: transform 0.3s ease;
  }
  
  .feature-card:hover {
    transform: translateY(-5px);
  }
  
  .feature-icon {
    color: #4786e6;
    font-size: 1.8rem;
    margin-bottom: 1rem;
  }
  
  .feature-content h4 {
    color: #2a5eb1;
    margin-bottom: 0.8rem;
  }
  
  /* Platform Roles */
  .roles-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1.5rem;
  }
  
  .role-card {
    background: #f8f9fa;
    border-radius: 10px;
    padding: 1.5rem;
    border-left: 4px solid #4786e6;
  }
  
  .role-header {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .role-icon {
    font-size: 1.5rem;
    color: #4786e6;
    margin-right: 1rem;
  }
  
  /* Impact Section */
  .metric-card {
    background: linear-gradient(135deg, #2a5eb1, #4786e6);
    color: white;
    padding: 2rem;
    border-radius: 12px;
    text-align: center;
  }
  
  .metric-card h3 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
  }
  
  .lead-impact-text {
    font-size: 1.1rem;
    line-height: 1.8;
    color: #444;
    max-width: 800px;
    margin: 2rem auto;
  }
  
  /* CTA Section */
  .cta-section {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 4rem 2rem;
    margin: 4rem 0;
    text-align: center;
  }
  
  .cta-title {
    font-size: 2rem;
    color: #2a5eb1;
    margin-bottom: 1rem;
  }
  
  .cta-subtitle {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 2rem;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .hero-title {
      font-size: 2rem;
    }
    
    .hero-subtitle {
      font-size: 1.1rem;
    }
    
    .section-title {
      font-size: 1.8rem;
    }
  }