
@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Raleway:ital,wght@0,100..900;1,100..900&family=Sora:wght@100..800&display=swap');

body {
  margin: 0;
  font-family: "Inter", serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body {
  overflow-x: hidden !important;
  width: 100% !important;
}

code {
  font-family: "Inter", serif;
}

.mTop {
  margin-top: 75px !important;
}

.teamMembers{
  height: 305px !important;
  width: 100%;
  object-fit: cover;
}

@media (max-width: 768px) {
  .teamMembers {
    height: 100% !important;
  }
}

.services{
  color: gray;
}
.content{
  color: black;
}
.content b{
  font-weight: 600;
  text-decoration: underline;
}

::-webkit-scrollbar {
  width: 5px
}
::-webkit-scrollbar-thumb {
  background: #4786E6;
}
::-webkit-scrollbar-track {
  background: white;
}

.text-primary1{
  color: rgb(18 24 32 / 40%) !important;
  font-size: 18px !important;
}

.aboutUsPara{
  color: rgb(18 24 32 / 40%) !important;
}

.team-card {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
}

.team-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.team-img-wrapper img {
  border: 4px solid #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.team-social {
  position: absolute;
  top: 10px;
  right: 10px;
}

.team-social .btn {
  transition: background 0.3s ease, transform 0.3s ease;
}

.team-social .btn:hover {
  background: #4786e6;
  color: #fff;
  transform: scale(1.1);
}

.react-tel-input .form-control{
  width: 100% !important;
}
