/* NavBar.css */
.services-dropdown {
  position: relative;
}

.dropdown-menu {
  width: 80vw;
  transform: translateX(-50%) !important;
  border: none !important;
  margin-top: 22px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.1);
  overflow: hidden;
}

.megaHead {
  border-radius: 5px;
  margin: 5px;
  padding: 15px;
  /* background: transparent; */
  transition: all 0.3s ease;
  cursor: pointer;
  border-left: 3px solid transparent;
  height: 100px !important;
}

.megaHead.active-service {
  background: rgba(255,255,255,0.1) !important;
  border-left: 4px solid #0d6efd;
}

.megaHead:hover {
  background: rgba(255,255,255,0.1) !important;
  border-left: 4px solid #0d6efd;
}

.megaHead h6 {
  font-size: 15px;
  color: #fff;
  font-weight: 600;
  margin-bottom: 5px;
}

.megaHead p {
  font-size: 12px;
  color: #aaa;
  line-height: 1.4;
}

.mega-menu-img {
  background-color: rgba(13, 110, 253, 0.1) !important;
  border-radius: 8px;
  display: flex;
  height: 56px;
  width: 56px;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.mega-menu-img img {
  height: 40px;
  width: auto;
  filter: invert(30%) sepia(98%) saturate(1600%) hue-rotate(210deg) brightness(95%) contrast(97%);
}

