.imgIconCont {
  padding: 35px 15px;
  transition: all 0.3s ease;
  border: 1px solid rgb(238, 233, 233);
  overflow-y: scroll;
  cursor: pointer;
}

/* Hover effect */
.imgIconCont:hover {
  transform: translateY(-5px);
  background: rgba(13, 110, 253, 0.03) !important;
  border-bottom: none;
  border-top: none;
}

/* Image container */
.mega-menu-img {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  padding: 10px;
  border-radius: 8px;
  transition: background 0.3s ease;
}

/* By default, the arrow is hidden */
.arrow-icon {
  color: transparent;
  transition: color 0.3s ease;
  font-size: 14px;
  display: none !important;
  margin-left: 8px;
}

/* Title container - This keeps title & arrow in the same line */
.service-title-container {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 8px; 
  text-decoration: none !important;
}

/* Title styles */
.service-title {
  color: #2c3e50;
  font-weight: 600;
  font-size: 15px;
  transition: color 0.3s ease;
  display: inline-block;
  text-decoration: none !important;
}

/* Description styles */
.service-description {
  font-size: 14px;
  color: #7f8c8d;
  line-height: 1.5;
  margin-bottom: 0;
  transition: color 0.3s ease;
}

/* Hover effects */
.imgIconCont:hover .mega-menu-img {
  background-color: #0d6efd !important;

}
.imgIconCont:hover .mega-menu-img img {
  filter: brightness(0) invert(1) !important;
}

.imgIconCont:hover .service-title {
  color: #0d6efd !important;
  margin-top: 13px !important;
  /* transition: 0.5s ease-in-out !important; */
}

.imgIconCont:hover .arrow-icon {
  transition: 0.5s ease-in-out !important;
  display: inline-block !important;
  color: #0d6efd !important;
  
}


@media (max-width: 768px) {

  .dropdown-menu{
    overflow: scroll !important;
    transform: translateX(0%) !important;
  }
  .col-md-3{
    height: auto !important;
  }

  .imgIconCont {
    padding: 51px 40px  !important;
  }
  .dropdown-menu.show {
    width: 100%;
    max-height: 70vh;
    overflow-y: auto;
  }
  
  .megaHead {
    padding: 10px;
  }
  
  .megaHead h6 {
    font-size: 14px;
  }
  
  .imgIconCont p {
    font-size: 13px;
  }
}

.nav-bar {
  background: #ffffff;
  box-shadow: 0 2px 15px rgba(0,0,0,0.1);
  padding: 10px 0;
}

.nav-link {
  color: #2c3e50 !important;
  font-weight: 500;
  transition: all 0.3s ease;
}

.nav-link:hover {
  color: #0d6efd !important;
}

.nav-link.active {
  color: #0d6efd !important;
  font-weight: 600;
}

/* Mobile Dropdown Styles */
@media (max-width: 768px) {
  .services-dropdown .dropdown-menu {
    position: static !important;
    transform: none !important;
    width: 100% !important;
    margin-top: 0 !important;
    border: none !important;
    box-shadow: none !important;
    padding: 0 !important;
  }

  .mobile-dropdown{
    border: 1px solid #d4d4d4;
    border-radius: 10px;
  }

  .mobile-dropdown .dropdown-item {
    padding: 10px 20px;
    font-size: 14px;
    color: #2c3e50;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    /* border-radius: 10px; */
    line-break: anywhere;
  }

  /* .mobile-dropdown .dropdown-item:hover {
    background: #f8f9fa;
  } */

  .mobile-dropdown .arrow {
    font-size: 12px;
  }

  .mobile-submenu {
    /* border: 1px solid gray; */
    /* border-radius: 10px; */
    width: 95%;
    overflow: hidden;
    word-wrap: break-word;
    padding-left: 10px;
  }

  .mobile-submenu .dropdown-item {
    padding: 8px 20px;
    font-size: 13px;
    color: #7f8c8d;
    white-space: normal;
    word-wrap: break-word; 
    overflow-wrap: break-word;
    /* border: 1px solid rgb(228, 226, 226); */
    border-radius: 5px;
  }

  .mobile-submenu .dropdown-item:hover {
    color: #0d6efd;
    background: transparent;
  }
}