.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  /* padding: 20px; */
}

.login-header {
  text-align: center;
  margin-bottom: 10px;
}

.login-header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #0d47a1;
}

.login-header p {
  color: #546e7a;
  font-size: 1rem;
}

.login-card {
  background: #ffffff;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form-label {
  font-size: 0.9rem;
  color: #0d47a1;
  font-weight: 600;
}

.form-input {
  padding: 12px 15px;
  border: 1px solid #cfd8dc;
  border-radius: 10px;
  font-size: 1rem;
  color: #546e7a;
  outline: none;
  transition: border 0.3s;
}

.form-input:focus {
  border-color: #42a5f5;
  box-shadow: 0px 0px 5px rgba(66, 165, 245, 0.5);
}

.form-button {
  background: #1e88e5;
  color: #ffffff;
  padding: 12px 15px;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s;
}

.form-button:hover {
  background: #1565c0;
}

.form-footer {
  text-align: right;
  margin-top: 10px;
}

.forgot-password {
  font-size: 0.8rem;
  color: #1e88e5;
  text-decoration: none;
}

.forgot-password:hover {
  text-decoration: underline;
}

.social-login {
  text-align: center;
  margin-top: 20px;
}

.social-login p {
  font-size: 0.9rem;
  color: #546e7a;
}

.social-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}

.social-btn {
  border: none;
  background: #e3f2fd;
  padding: 10px 15px;
  border-radius: 10px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s, background 0.3s;
}

.social-btn:hover {
  transform: scale(1.1);
  background: #bbdefb;
}

.google {
  color: #db4437;
}

.apple {
  color: #000000;
}

.twitter {
  color: #1da1f2;
}
