
.projects-list {
  display: flex;
  flex-direction: column; 
}

.project-strip {
  display: flex; 
  justify-content: space-between;
  align-items: center;
  border-radius: 15px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.8), rgba(0, 150, 255, 0.1)); 
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-image {
  flex: 1;
  max-width: 45%;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.project-description {
  flex: 1.5;
  max-width: 50%;
  padding-left: 20px;
  color: #333;
}

.project-description h3 {
  font-size: 1.75rem;
  margin-bottom: 15px;
  color: #1e2a38;
}

.project-description p {
  font-size: 1rem;
  margin-bottom: 20px;
  line-height: 1.6;
}

.project-buttons {
  display: flex;
  gap: 15px;
}

.project-buttons a {
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.project-buttons a:hover {
  transform: scale(1.05);
}

.project-buttons .btn-primary {
  background-color: #007bff;
  color: white;
  border: none;
}

.project-buttons .btn-primary:hover {
  background-color: #0056b3;
}

.project-buttons .btn-secondary {
  background-color: #6c757d;
  color: white;
  border: none;
}

.project-buttons .btn-secondary:hover {
  background-color: #495057;
}

.projectContainer{
  border-radius: 10px;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  background: linear-gradient(90deg, rgba(228, 237, 255, 0.3) 0%, rgba(64, 124, 238, 0.3) 100%);

}

@media (max-width: 768px) {
  .project-strip {
    flex-direction: column;
    text-align: center;
    padding: 15px;
  }

  .project-image {
    max-width: 100%;
    margin-bottom: 15px;
  }

  .project-description {
    max-width: 100%;
    padding-left: 0;
  }

  .project-description h3 {
    font-size: 1.5rem;
  }
}

.projectContainer {
  display: flex;
  flex-wrap: wrap;
}

.projectContainer.flex-row-reverse {
  flex-direction: row-reverse;
}

.modal-open {
  overflow: hidden;
}

